import React from 'react';
import PropTypes from 'prop-types';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Tabbar from "@vkontakte/vkui/dist/components/Tabbar/Tabbar";
import TabbarItem from "@vkontakte/vkui/dist/components/TabbarItem/TabbarItem";

import { Icon28NewsfeedOutline, Icon28MessageOutline, Icon28ClipOutline, Icon28SettingsOutline} from '@vkontakte/icons'

const Home = ({ id, go }) => (
	<Panel id={id}>
    <PanelHeader
        right={<PanelHeaderButton onClick={go} data-to="settings">
            <Icon28SettingsOutline /> 
        </PanelHeaderButton>}>Главная</PanelHeader>
	<Tabbar>
      <TabbarItem text="Главная">
        <Icon28NewsfeedOutline />
      </TabbarItem>
      <TabbarItem text="Персик" onClick={go} data-to="persik">
        <Icon28MessageOutline />
      </TabbarItem>
      <TabbarItem text="Еще">
        <Icon28ClipOutline />
      </TabbarItem>
    </Tabbar>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

export default Home;
